import { ChevronLeftIcon } from "@chakra-ui/icons";
import { Center, Flex, Input, InputGroup, Radio, RadioGroup } from "@chakra-ui/react";
import { useState } from "react";
import { useHistory } from "react-router-dom";
import { useAppSelector } from "src/hooks/useStore";
import { globalSelector } from "../../store/global";

const typeRp = ["Fake", "Scam", "Malware", "Prohibited", "Phishing", "Other"];

export default function Report() {
  const { searchKey } = useAppSelector(globalSelector);
  const [search, setSearch] = useState<string>(searchKey);
  const [type, setType] = useState<string>();
  const history = useHistory();

  return (
    <Flex direction="column" position="relative">
      <ChevronLeftIcon
        position="absolute"
        boxSize={8}
        top={3}
        left={2}
        cursor="pointer"
        onClick={() => {
          history.goBack();
        }}
      />
      <Center color="primary.200" fontWeight="semibold" mt={3} fontSize="xl">
        Report this website
      </Center>
      <InputGroup>
        <Input
          background="#F7F7F7"
          value={search}
          onChange={(e) => {
            setSearch(e.target.value);
          }}
          mx={4}
          mt={4}
        />
      </InputGroup>
      <RadioGroup onChange={setType} value={type}>
        <Flex direction="column" justify="center" align="center" mx={2} mt={2}>
          <Flex wrap="wrap" justify="center">
            {typeRp.map((item) => (
              <Radio value>
                {item}
              </Radio>
            ))}
          </Flex>
        </Flex>
      </RadioGroup>
    </Flex>
  );
}
